.sv-table tr ~ td {
  padding: 0;
}
.sv-table tr .sv-matrixmultiple-table__cell,
  .sv-table tr:first-child .sv-matrixmultiple-table__cell,
  .sv-table tbody tr:last-child .sv-matrixmultiple-table__cell {
  padding: 0.9375em;
  vertical-align: middle;
  padding-left: 0;
}
.sv-table tr .sv-dropdown {
  color: var(--grey);
  border: 1px solid var(--light-grey);
  line-height: 18.2px;
  height: 27px;
  padding-left: 4px;
}
.sv-matrix-radio-selectbase__label {
  line-height: 18.2px;
  height: 20px;
}
.sv-table .sv-form-control .sv-item {
  margin: 0;
  display: block;
}
