.text_inputer {
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */
  padding: 14px 5px;
  border: 1px solid #aaa;
  outline: none;
  height: 16px;
  width: 100%;
  color: #808080;
  margin-bottom: 0px;
  text-indent: 10px;
  font-size: 14px;
}
.read-only {
  border: none;
  background-color: unset;
  text-align: left !important;
}

.container .sv-root-modern .sv-question__title--answer {
  background-color: unset;
}

.sv-form-control .sv-question__header--location--top {
  margin-bottom: 0;
}

.sv-form-control .sv-custom-description {
  color: var(--grey);
  margin-bottom: 15px;
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

#counterForm .read-only {
  text-indent: 0;
  padding-left: 0;
}
