.sv-matrix-control {
  overflow: auto;
  box-sizing: border-box;
  font-size: 14px;
  font-weight: 400;
}
.sv-custom-matrix-button {
  background-color: var(--primary);
  border-radius: 0;
  height: 30px;
  width: 100%;
  text-align: center;
}
.sv-custom-matrix-button:hover {
  background-color: var(--black);
}
.sv-root-modern .sv-matrixdynamic .sv-matrix-dynamic-custom-btn__remove {
  background-color: transparent;
  border: 2px solid var(--black);
  color: var(--black);
}
.sv-root-modern .sv-matrixdynamic .sv-matrix-dynamic-custom-btn__remove:hover {
  background-color: var(--black);
  color: var(--white);
}
.sv-form-control .sv-table.sv-matrixdynamic {
  max-width: 100%;
}
.sv-form-control .sv-table.sv-matrixdynamic td:first-child,
.sv-form-control .sv-table.sv-matrixdynamic th:first-child {
  padding-left: 0;
}
.sv-form-control .sv-table.sv-matrixdynamic .sv-table__cell {
  min-width: unset;
}
.sv-form-control .sv-table.sv-matrixdynamic .sv-action-bar,
.sv-form-control .sv-table.sv-matrixdynamic .sv-table__cell:last-child {
  padding-right: 0;
}
.sv-form-control .sv-table.sv-matrixdynamic .sv-action {
  width: 100%;
}
