.sv-multipletext-field__cell {
  width: 50%;
  display: contents;
}
.sv-multipletext__row {
  display: flex;
  justify-content: space-between;
}
.sv-multipletext__item-title {
  margin-right: 10px;
  margin-bottom: 1em;
}
@media screen and (max-width: 1025px) {
  .sv-multipletext__row {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }
}
@media screen and (max-width: 700px) {
  .sv-multipletext__row {
    display: flex;
    height: auto;
    flex-wrap: wrap;
    flex-direction: column;
  }
}
@media screen and (max-width: 320px) {
  .sv-multipletext__row {
    display: flex;
    height: auto;
    flex-wrap: wrap;
    flex-direction: column;
  }
  .sv-multipletext-field__cell {
    margin-right: 10px;
    height: 27.983px;
  }
}
