.sv-radio-group-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.sv-root-modern .sv-container-modern .sv-radio--checked .sv-radio__svg {
  border-color: var(--dark-grey);
  fill: var(--primary);
}

.sv-question-radio__content {
  margin-left: 2px;
}

.sv-radio__svg {
  margin-left: -6px;
  border: 1px solid var(--dark-grey);
  border-radius: 100%;
  width: 20px;
  height: 20px;
}

.sv-radio-label {
  color: var(--grey);
  display: flex;
  align-items: center;
}

.sv-panel .sv-question__title {
  margin-left: 9px;
  font-weight: normal;
  font-size: 14px;
}

.sv-radio-group-wrapper .sv-radio-selectbase__label {
  display: flex;
  flex-wrap: wrap;
  line-height: 20px;
  padding-left: 5px;
}

.sv-radio-group-wrapper .sv-radio-group-item {
  flex-basis: 240px;
}

.sv-radio-group-wrapper .sv-selectbase__item {
  padding: 10px 0;
  margin-bottom: 0;
}

.sv-radio-group-wrapper .sv-item__decorator {
  height: 20px;
  width: 20px;
}
