.sv-row .sv-panel__lv2 {
  padding-bottom: 3px;
  font-size: 16px;
  color: var(--primary);
  background-color: var(--white);
  text-indent: 0;
  padding-left: 0;
}
.sv-row .with-border {
  font-size: 18px;
  padding-left: 12px;
}
.sv-panel .sv-question__title {
  padding-left: 0;
  margin-left: 0;
  font-weight: normal;
  font-size: 14px;
}
.sv-root-modern .sv-panel__description {
  padding-left: 0;
  font-size: 14px;
  line-height: 26px;
  color: var(--grey);
}
.sv-panel__border-around {
  font-size: 18px;
  margin-bottom: 40px;
  padding: 0 7px 7px;
  border: 2px solid var(--primary);
}
.sv-panel {
  margin-bottom: 40px;
}
.sv-panel__border-around .sv-panel__content {
  padding: 10px;
}
.sv-panel .sv-panel {
  margin-bottom: 20px;
}
.sv-panel .sv-panel:last-child {
  margin-bottom: 0px;
}

.sv-root-modern .sv-description {
  margin-top: 12px;
}
