.sv-table {
  max-width: 90%;
  max-height: 10%;
  border-collapse: separate;
  border-spacing: 0;
  background-color: transparent;
}
.sv-table > tbody > tr > td:first-child {
  padding-left: 0;
}
.sv-matrix {
  font-size: 14px;
  font-style: normal;
  font-variant: normal;
  letter-spacing: normal;
}
.sv-table__cell {
  padding: 0.9375em 0;
  box-sizing: content-box;
}
.header-cell {
  text-align: center;
  padding: 0;
  font-weight: normal;
  font-size: 14px;
}
.sv-item__decorator .sv-radio__decorator {
  border: none;
  height: unset;
}
.sv-root-modern .sv-item__control:focus + .sv-item__decorator {
  border: none;
}
