.sv-checkbox-wrapper {
	display: flex;
	flex-wrap: wrap;
}

.sv-checkbox-wrapper .sv-checkbox-group-item {
	flex-basis: 240px;
	margin-bottom: 5px;
}

.sv-checkbox-wrapper .sv-selectbase__item {
	margin-bottom: 5px;
}

.sv-checkbox-wrapper .sv-item__control-label {
	color: var(--grey);
	line-height: 20px;
}

.sv-checkbox-wrapper .sv-checkbox__svg {
	border: 1px solid var(--dark-grey);
	border-radius: 0;
	width: 14px;
	height: 14px;
}

.sv-root-modern .sv-item__control:focus + .sv-item__decorator {
	border: 0;
}

body .sv-root-modern .sv-checkbox--checked .sv-checkbox__svg {
	background-color: var(--primary);
	fill: var(--white);
}

.sv-form-control .sv-item .sv-selectbase-single__label {
	line-height: 18.2px;
	margin: 0px 0px 5px;
}

[type='radio']:checked,
[type='radio']:not(:checked) {
	position: absolute;
	left: -9999px;
}
